<template>  
<div class="container">
 <div class="columns is-desktop is-multiline is-centered">
 <div class="column is-8 is-half">
 <div class="login is-centered">
 <div class="login-header">
 <img src="@/assets/download.png" width="120" height="120" alt="" class="is-center">
 <h1>Login M-Food Restaurant</h1>
 </div>
  <a-form
    id="components-form-demo-normal-login"
    :form="form"
    class="login-form"
    @submit="validateBeforeSubmit">
    <a-form-item>
      <a-input
        v-decorator="[
          'email',
          { rules: [{ required: true, message: 'Please input your username!' }] },
        ]"
        placeholder="Email" name="email"  style="height:40px;"
      >
        <a-icon slot="prefix" type="mail" style="color: rgba(0,0,0,.25)" />
      </a-input>
    </a-form-item>
    <a-form-item>
      <a-input-password
        v-decorator="[
          'password',
          { rules: [{ required: true, message: 'Please input your Password!' }] },
        ]"
        type="password" placeholder="Password" name="password"  style="height:40px;"
      >
        <a-icon slot="prefix" type="lock" style="color: rgba(0,0,0,.25)" />
      </a-input-password>
    </a-form-item>
           <p class="msg_login" v-if="msg_login">ລະຫັດຜ່ານ ຫຼື ອີເມວບໍ່ຖືກຕ້ອງ</p>
    <a-form-item>
      <a-button type="primary" html-type="submit" :loading="loading" class="login-form-button" style="height:40px;">
        ເຂົ້າສູ່ລະບົບ
      </a-button>
    </a-form-item>
  </a-form>
</div>
 </div>
 </div>
 </div>    
</template>

<script>
import axios from 'axios';
export default {
data(){
  return {
            email:'',
            password:'',
            loading: false,
            iconLoading: false,
            btn_login: true,
            btn_check: false,
            msg_login: false,
  }
},
  
  beforeCreate() {
    this.form = this.$form.createForm(this, { name: 'normal_login' });
  },
  methods: {
            // Validate Form
        validateBeforeSubmit(e) {
          e.preventDefault();
            this.form.validateFields().then((result) => {
                if (result) {
                      this.loading= false;
                     this.AdminLogin(result);
                }
            });
        },
          AdminLogin(result){
            this.$store.dispatch('AdminLogin', {
              email: result.email,
              password: result.password
            }).then(() => {
                const config = {
                    headers: {'Authorization': "Bearer " + this.$store.getters.getToken}
                };
                axios.get(this.Url + 'get/admin/', config).then((res) => {
                    const admin = res && res.data && res.data.admin;
                    this.$store.commit('setUserProfile', admin);
                    window.localStorage.setItem('admin_profile', JSON.stringify(admin));
                }).catch(() => {
                    window.localStorage.removeItem('admin_profile');
                    this.$router.push( { name: 'dashboard' });
                })
            }).catch(error => {
                 if(error.response.status == 400){
                     this.msg_login = true;
                    setTimeout(()=>{
                         this.msg_login = false;
                    }, 2500)
                 }
            });
        }
  },
};
</script>
