import Vue from 'vue'
import Vuex from 'vuex'
import axios from 'axios'
import router from '@/route'
// import router from './router'
Vue.use(Vuex)
let Url = process.env.VUE_APP_BASE_API_URL;
export default new Vuex.Store({
    state: {
        token: localStorage.getItem('access_token') || null,
        msg_err_login: '',
        userProfile: {},
    },
    getters:{
        LoggedIn(state) {
            return state.token !== null
        },
        getUserType(state) {
            let profile = window.localStorage.getItem('user_profile');
            if (profile) {
                try {
                    profile = JSON.parse(profile);
                } catch (err) {
                    profile = {}
                }
            } else {
                profile = {};
            }
            return state.userProfile.type || profile.type;
        },
        getToken(state) {
          return state.token;
        },
        isAuth(state) {
          return state.token && state.token !== null
        }
    },
    mutations: {
        SignIn(state, token) {
          state.token = token
        },
        destroyToken(state) {
            state.token = null;
        },
        Msg_error_login(state, msg){
          state.msg_err_login = msg
        },
        setUserProfile(state, payload) {
          state.userProfile = payload;
        },
        setToken(state, accessToken) {
          state.token = accessToken
        }
      },

    actions: {
            // LOGIN ເພື່ອໃຫ້ໄດ້ Token ມາໃຊ້ໃນການເຂົ້າຫາຂໍ້ມູນທີ່ຕ້ອງການ...
            AdminLogin(context, data) {
                return new Promise((resolve, reject) => {
                    axios.post(Url + 'sign-in', {
                        email: data.email,
                        password: data.password
                    })
                        .then(response => {
                            resolve(response)
                            const token = response.data.access_token;
                            localStorage.setItem('access_token', token)   // ເກັບ Token ໄວ້ໃນ Localstorage ເພື່ອຈະນຳໄປໃຊ້ຂໍຂໍ້ມູນ
                            context.commit('SignIn', token)
                            context.commit('setUserProfile', response.data.user);
                            window.localStorage.setItem('user_profile', JSON.stringify(response.data.user));

                        })
                        .catch(error => {
                            reject(error)
                        })
                })
            },
                    // LOG OUT
        destroyToken(context) {
            if (context.getters.LoggedIn) {
                return new Promise((resolve, reject) => {
                    axios.defaults.headers.common['Authorization'] = 'Bearer ' + context.state.token
                    axios.post(Url + 'sign-out')
                        .then(response => {
                            resolve(response)
                        })
                        .catch((error) => {
                            reject(error)
                        }).finally(() => {
                        localStorage.removeItem('access_token')     // Remove Item Of Localstorage...
                        context.commit('destroyToken')
                        window.localStorage.removeItem('user_profile')
                        router.push({
                            name: 'login'
                        }).catch(() => {
                        });
                    }) 
                })
            }
        },
    }
})